@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.flex {
  display: flex;
}
.items-center {
  align-items: center;
}
.items-center {
  justify-content: space-between;
}
.btn {
  padding: 0.8rem 2.5rem;
  font-size: 1rem;
  font-weight: 600;
  border: 2px solid transparent;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
}
.btn-primary {
  background: var(--primary);
  color: var(--secondary);
}
.hero .left {
  color: var(--white);
  font-size: 2rem;
}
.icons {
  margin-top: 20px;
}
.justify-between {
  justify-content: space-between;
}
.justify-evenly {
  justify-content: space-evenly;
}
.justify-center {
  justify-content: center;
}
.flex-1 {
  flex: 1 1;
}
input {
  width: 100%;
  padding: 5px;
  margin: 10px;
}

:root {
  --primary: #3dcfd3;
  --dark: #161616;
  --white: #ffffff;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
}
body {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1152px;
  padding: 0 15px;
  margin: 0 auto;
}

.section1 {
  background-color: #161616;
  background-color: var(--dark);
  height: 1250px;
}
/* Navbar */
.menu {
  display: none;
}
#mainbtn {
  display: flex;
  justify-content: center;
  text-align: center;

  padding-left: 20px;
  width: 100%;
  margin: 0 auto;
}
.mobilebtn {
  text-align: center;
  justify-content: center;
  padding-left: 20px;
}
.contactmebtn {
  display: none;
}

header nav {
  padding: 2rem 0;
}

header nav .left a {
  color: #ffffff;
  color: var(--white);
  text-decoration: none;
  margin-right: 2rem;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
.menu {
  align-items: center;
}

/* Layer 1 */
.wrapper {
  width: 100%;
}
.img-info {
  width: 100%;
  text-align: center;
}
.img-info > h4 {
  padding: 30px 30px 20px;
  font-size: 30px;
  color: white;
  line-height: 24px;
}
.img-info > p {
  padding: 0px 30px 20px;
  font-size: 70px;
  color: white;
}
.img-info > h6 {
  padding: 10px 0px 0px;
  font-size: 30px;
  color: white;
  line-height: 44px;
}
.img-me {
  width: 100%;
  height: 600px;
}

.icon {
  width: 10% !important;
  padding: 5px;
  margin-right: 20px;
  margin-bottom: 20px;
  color: white;
  border-radius: 50%;
  background-color: white;
}

.layer1 {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.layer1img {
  height: 100vh;
  width: 100vh;
  background-repeat: no-repeat;
}

.layer1text {
  padding: 120px 0px 10px 0px;
  width: 100vh;
  height: 100vh;
  display: block;
  margin: 0 auto;
  text-align: center;
}

.myButton {
  border-radius: 28px;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Trebuchet MS;
  font-size: 17px;
  padding: 16px 23px;
  text-decoration: none;
  margin-bottom: 20px;
  background-color: #ee8c68;
  background-image: linear-gradient(315deg, #ee8c68 0%, #eb6b9d 74%);
}

/*About Me */

.Desktop {
  height: 20%;
}
.about {
  height: 800px;
  padding: 0px 20px 0px 20px;
}

.about h1 {
  margin-bottom: 1rem;
  font-size: 3.2rem;
  font-weight: 400;
}
.about p {
  font-family: "Padauk", sans-serif;
  line-height: 1.5;
}
.aboutmeheader {
  padding-top: 70px;
  text-align: left;
  margin-bottom: 1rem;
  font-size: 3.6rem;
  font-weight: 400;
}
.aboutmeheader2 {
  color: #3dcfd3;
}
.about-img {
  padding-top: 30px;
  width: 100%;
}

/* Projects */
.projectstext {
  text-align: right;
  padding-top: 14%;
  padding-bottom: 5%;
  color: white;
  margin-bottom: 1rem;
  font-size: 3.2rem;
  font-weight: 400;
}
.projectstext2 {
  color: #3dcfd3;
}
.projects {
  background-color: #161616;
  background-color: var(--dark);
  padding-bottom: 5%;
}

.card {
  margin: 10px;
  border-radius: 10px;
  flex: 2.5 1;
  transition: transform 100ms ease-in;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.07);
}
.card > img {
  height: 250px;
  width: 100%;
  margin: 0 auto;
}
.card_info {
  margin: 10px;
  flex: 1 1;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  padding: 20px;
}
.card_info > h2 {
  font-size: 28px;
  font-weight: 200;
  text-align: center;
}
.card_info > h3 {
  font-size: 16px;
  font-weight: 200;
  font-family: "Roboto", sans-serif;
}

.projects > .container > p {
  color: white;
  font-size: 16px;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  text-align: center;
}
/* .right > img {
  width: 300px;
  height: 400px;
} */

.footer {
  padding-top: 40px;
  background-color: #161616;
  padding-bottom: 40px;
}
.footer > h1 {
  color: white;
  text-align: center;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: 200;
}
